<template>
    <div class="progress-container">
        <div class="progress-bar" :style="`width: ${getProgress + 1}%`"></div>
        {{ getSelectedQuestionIndex + 1 }}/{{ allQuestions.length }}
    </div>
</template>

<script>
import { mapGetters } from "vuex";

export default {
  name: "ProgressBar",
  computed: mapGetters([
    "allQuestions",
    "getSelectedQuestionIndex",
    "getProgress"
  ])
};
</script>

<style lang="sass">
.progress-container
    position: fixed
    top: 0
    width: 100%
    height: 1rem
    text-align: center
    .progress-bar
        position: absolute
        z-index: -1
        top: 0
        left: 0
        width: 0%
        height: 100%
        background: #21bf73
        transition: width .5s linear
</style>
