<template>
    <div class="result page">
        <h1>You have finished the exam</h1>
        <ul>
            <li>
                Number of questoins: {{ allQuestions.length }}
            </li>
            <li>
                Correct Questions: {{ correctQuestions.length }}
            </li>
            <li>
                Wrong Questions: {{ wrongQuestions.length }}
            </li>
            <li>
               Status: <span v-if="correctQuestions.length >= (allQuestions.length / 2)">Passed</span> <span v-else>Failed</span>
            </li>
            <li>
               Score: {{ Math.round(((correctQuestions.length * 100) / allQuestions.length)) }}%
            </li>
        </ul>
        <router-link to="/">
            <button class="route-btn">Back to home</button>
        </router-link>
    </div>
</template>

<script>
import { mapGetters } from "vuex";

export default {
  name: "Finish",
  computed: mapGetters(["allQuestions", "correctQuestions", "wrongQuestions"])
};
</script>

<style lang="sass">
.result
    ul li
        margin-bottom: 1rem
    button
        margin-top: 1rem
</style>

